import React from 'react'
import gift from './../image/birthday-gift.jpg'
function Birthday22() {
    return (
        <section className="content-wapper">
        <figure>
          <img className="photo" src={gift} alt="22" />
          <figcaption>22</figcaption>
        </figure>
        <h1 className="title">22</h1>
        <span className="post-date">30 May 2021</span>
        <article className="content">
          <br></br>
          
        </article>
      </section>
    )
}

export default Birthday22



