import React from "react";
import myRoom from "./../image/my-room.jpg";

import { Link } from "react-router-dom";

function AllContent() {
  return (
      <section className="content-wapper">
        <figure>
          <img className="photo" src={myRoom} alt=""/>
          <figcaption></figcaption>
        </figure>
        <h1 className="title"></h1>
        <span className="post-date">May 2021</span>
        <nav className="all-content-nav">
          <ul className="All-Content-list">
            <li>
              <Link to="/NoFriend">??</Link>
            </li>
            <li>
              <Link to="/Covid19AtHome">...</Link>
            </li>
            <li>
            <Link to="/Birthday22">22</Link>
            </li>
          </ul>
        </nav>
      </section>
  );
}

export default AllContent;



