import React from "react";
import Handsite from './../image/hand.jpg'

function NameAndPhoto() {
  return (
      <section className="side">
        <h3 className="photoName">
          <p>Jimmy Hung</p>
        </h3>
        <div className="photoSticker">
          <img src={Handsite} alt="me" />
        </div>
        <div className="intro">
          <p>
          <br />
          </p>
        </div>
      </section>
  );
}

export default NameAndPhoto;



